import { IArticleState } from "./article.store.states";
import {ARTICLE_STORE_NAME} from "./article.store.name";

export const setArticlesAction = (param: IArticleState) => ({
  type: ARTICLE_STORE_NAME,
  reducer: (state: IArticleState): IArticleState => ({
    data: param.data,
    education: param.education,
    experiencie: param.experiencie,
    profile: param.profile,
    skills: param.skills
  })
});
