import React from 'react'
import { IContainer } from './container.interface'
import Element from '../components/elements/Elements'


const ProfileContainer = (props: IContainer) => {

    const profileArticle = props.store.articleStore.profile

    return (
        <Element.wrapper size='xl'>
            <Element.frame>
                <Element.image src={profileArticle.photo} />
            </Element.frame>
            <Element.title>{profileArticle.name}</Element.title>
            {profileArticle.roles.map(rol => (
                <Element.resume key={rol}>{rol}</Element.resume>
            ))}
            <Element.space primary />
            <Element.separator />
            <Element.space primary />
            {profileArticle.details.map(detail => (
                <Element.item key={detail}>{detail}</Element.item>
            ))}
        </Element.wrapper>

    )
}

export default ProfileContainer