import React from 'react'
import Elements from './elements/Elements'
import { } from 'react-router'

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

interface INav {
    currentNavLocation: string
}

const Nav = (props: INav) => {
    return (
        <Elements.nav>
            <Elements.navItem isActive={props.currentNavLocation === "/profile"}><Link to="/profile">Perfil</Link></Elements.navItem>
            <Elements.navItem isActive={props.currentNavLocation === "/skills"}><Link to="/skills">Conocimientos y Habilidades</Link></Elements.navItem>
            <Elements.navItem isActive={props.currentNavLocation === "/experience"}><Link to="/experience">Experiencia</Link></Elements.navItem>
            <Elements.navItem isActive={props.currentNavLocation === "/data"}><Link to="/data">Datos</Link></Elements.navItem>
            <Elements.navItem isActive={props.currentNavLocation === "/education"}><Link to="/education">Educación</Link></Elements.navItem>
        </Elements.nav>
    )
}

export default Nav 