import React from 'react'
import { IContainer } from './container.interface'
import Element from '../components/elements/Elements'
import { ISkill } from '../stores/articlesStore/article.store.interface'


const SkillsContainer = (props: IContainer) => {
    const skillList = props.store.articleStore.skills

    const generateTags = (tags: ISkill['tags']) => tags.map(tag => (<Element.tag key={tag}>{tag}</Element.tag>))

    const generateSkill = (skill: ISkill) => (
        <>
            <Element.title>{skill.title}</Element.title>
            <Element.tags>
                {generateTags(skill.tags)}
            </Element.tags>
        </>
    )

    const generateSkills = (
        skills: IContainer['store']['articleStore']['skills']
    ) => skills.map((skill, index) => (
        <div key={skill.title}>
            {generateSkill(skill)}
            {index + 1 !== skillList.length && <Element.separator />}
        </div>
    ))

    return (
        <Element.wrapper size='xl'>
            {generateSkills(skillList)}
        </Element.wrapper>
    )
}

export default SkillsContainer