import store from '../rootStore';
import { setArticlesAction } from './article.store.action'
import {IArticleState} from './article.store.states'

import {
  getDataArticle,
  getEducationArticle,
  getExperiencieArticle,
  getProfileArticle,
  getSkillsArticle
} from "./article.store.services";

export const getAllArticles = async () => {
  const [
    education,
    data,
    experiencie,
    profile,
    skills
  ] = await Promise.all([
    getEducationArticle(),
    getDataArticle(),
    getExperiencieArticle(),
    getProfileArticle(),
    getSkillsArticle()
  ])

  const articles = {
    data: data,
    education:education,
    experiencie: experiencie,
    profile: profile,
    skills: skills
  } as IArticleState

  store.dispatch(setArticlesAction(articles))
}
