import React from "react";
import { Routes } from "./routes/Routes";
import Element from './components/elements/Elements'
import NavContainer from './containers/Nav.container'
import { initAndLoadApp } from './business/initAndLoadApp'

const App = () => {
    React.useEffect(() => {
        initAndLoadApp()
    }, [])

    return (
        <Element.app>
            <NavContainer />
            <Element.content>
                <Routes />
            </Element.content>
        </Element.app>
    )
}


export default App;
