import React from 'react'
import { IContainer } from './container.interface'
import Element from '../components/elements/Elements'
import { IData } from '../stores/articlesStore/article.store.interface'

const DataContainer = (props: IContainer) => {

    const dataList = props.store.articleStore.data


    const generateDataItems = (
        dataList: IContainer['store']['articleStore']['data']
    ) => dataList.map((data, index) => (
        <div key={data.label + data.value}>
            <Element.space primary />
            <Element.label>{data.label}:</Element.label>
            <Element.space secundary />
            <Element.value>{data.value}</Element.value>
            <Element.space secundary />
            {index + 1 !== dataList.length && <Element.separator />}
        </div>
    ))

    return (
        <Element.wrapper size='xl'>
            {generateDataItems(dataList)}
        </Element.wrapper>
    )
}

export default DataContainer

