import * as React from "react";
//import { AnimatedSwitch } from "react-router-transition";
import { Switch } from "react-router-dom";
import { RouteConnected } from "./RouteConnected";

interface IRoute {
  path: string;
  container: any;
}

export const RoutesGenerator = (props: { routeList: Array<IRoute> }) => {
  const generateRoutes = () => props.routeList.map(route => (
    <RouteConnected
      path={route.path}
      container={route.container}
      key={`route${route.path}`}
    />
  ));

  return (
    <Switch>
      {generateRoutes()}
    </Switch>
  );
};
